import { cn } from '@/utils/cn';
import React from 'react';

type FlexTableRowProps = {
  rounded?: 'none' | 'sm' | 'md' | 'lg';
  children: React.ReactNode;
  border?: boolean;
  className?: string;
};

const FlexTableRow = ({ className, rounded, border = true, children }: FlexTableRowProps) => {
  return (
    <div
      className={cn(
        `${border && 'border'} w-full overflow-x-auto border-muted-200 dark:border-muted-700 dark:bg-muted-800 flex flex-col  bg-white md:flex-row  md:justify-between "
        ${
          rounded == 'sm'
            ? `rounded-md`
            : rounded == 'md'
              ? 'rounded-lg'
              : rounded == 'lg'
                ? 'rounded-xl'
                : ''
        }
    `,
        className && className
      )}
    >
      {children}
    </div>
  );
};

export default FlexTableRow;
